import {
  Box,
  Divider,
  IconButton,
  InputBase,
  Link,
  Paper,
} from "@mui/material";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import InstagramIcon from "@mui/icons-material/Instagram";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import ForwardToInboxIcon from '@mui/icons-material/ForwardToInbox';
import React from "react";
import "./LandingPage.scss";

const LandingPage = function () {
  return (
    <div style={{ width: "100%", height: "100vh", backgroundColor: "#0d0e12" }}>
      <Box
        sx={{
          width: "100%",
          backgroundColor: "#0d0e12",
          color: "#FAF9F6",
          fontSize: "1.2rem",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            width: "75%",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            paddingTop: "3rem",
          }}
        >
          {/* <div>
                    <Link href="null">Ravi</Link>
                </div> */}
          <div>
            <Link href="null" className="btn-hover-effect">
              home
            </Link>
            <Link href="null" className="btn-hover-effect">
              about
            </Link>
            <Link href="null" className="btn-hover-effect">
              blog
            </Link>
            <Link href="null" className="btn-hover-effect">
              contact
            </Link>
          </div>
          <div>
            <div>
              <Link href="null" className="btn-hover-effect-hire-me">
                hire me <ArrowForwardIosIcon sx={{ fontSize: "10px" }} />{" "}
              </Link>
            </div>
          </div>
        </div>
      </Box>
      <Box className="main-content-container">
        <div className="main-content-placement">
          <div>
            <div style={{ fontSize: "38px", fontWeight: "300" }}>Hey, I am</div>
            <div style={{ fontSize: "78px" }}>Ravi Prakash</div>
            <div
              style={{
                fontSize: "18px",
                color: "grey",
                letterSpacing: ".25px",
              }}
            >
              Building stuff is what a coder does?
            </div>
          </div>
          <div style={{ paddingTop: "2rem" }}>
            <div style={{ fontSize: "30px" }}>Hola,</div>
            <div style={{ fontSize: "16px", paddingTop: "10px" }}>
              Have you read my latest blog post?
            </div>
            <div style={{ fontSize: "16px", paddingTop: "10px" }}>
              <Link
                href="null"
                style={{ textDecoration: "none", color: "#FAF9F6" }}
              >
                check out <ArrowForwardIosIcon sx={{ fontSize: "10px" }} />
              </Link>
            </div>
          </div>
        </div>
      </Box>

      <Box className="main-content-container">
        <div className="main-content-placement-1" style={{width:'50%'}}>
          <div style={{border:'1px solid white', borderRadius:'5px', padding:'10px', margin:'10px 0px 10px 0px'}}>
          <div className="project-box" style={{paddingTop:'1.5rem'}}>
            <div style={{ paddingBottom: "20px" }}>
              Have some project in mind, lets discuss?
            </div>
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "flex-start",
              }}
            >
              <Paper
                component="form"
                sx={{
                  p: "2px 4px",
                  display: "flex",
                  alignItems: "center",
                  width: 380,
                  color:'white',
                  backgroundColor:'grey',
                  // border:'1px solid white',
                  borderRadius:'10px'
                }}
              >
                <InputBase
                  sx={{ ml: 1, flex: 1 }}
                  placeholder="test@outlook.com"
                  inputProps={{ "aria-label": "search google maps" }}
                />
                <Divider sx={{ height: 28, m: 0.5, color:'white' }} orientation="vertical" />
                <IconButton
                  color="primary"
                  sx={{ p: "10px" }}
                  aria-label="directions"
                >
                  <ForwardToInboxIcon sx={{color:'white'}}/>
                </IconButton>
              </Paper>
            </div>
          </div>
        </div>
          </div>
          <div style={{ paddingTop: "0rem" }}>
            <div>
              <img src="/image/boat.png" alt="boat" style={{height:'160px'}}/>
              </div>
              <div style={{ paddingTop: "1.8rem" }}>
              <IconButton>
                <LinkedInIcon className="icon-btn" />
              </IconButton>
              <IconButton>
                <GitHubIcon className="icon-btn" />
              </IconButton>
              <IconButton className="icon-btn">
                <img src="/image/medium-logo.png" alt="medium" style={{backgroundColor:'white', height:'21px'}}/>
              </IconButton>
              <IconButton>
                <InstagramIcon className="icon-btn" />
              </IconButton>
            </div>
        </div>
      </Box>

      <Box
        sx={{
          width: "100%",
          backgroundColor: "#0d0e12",
          color: "#FAF9F6",
          fontSize: "1.2rem",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            width: "70%",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            paddingTop: "4rem",
          }}
        >
          <div
            style={{ fontSize: "14px", color: "grey", letterSpacing: ".48px" }}
          >
            All right reserved @2023
          </div>
          <div
            style={{ fontSize: "14px", color: "grey", letterSpacing: ".48px" }}
          >
            <Link href="null" style={{ textDecoration: "none", color: "grey" }}>
              FAQ
            </Link>
          </div>
        </div>
      </Box>
    </div>
  );
};
export default LandingPage;
